*{font-family:'Lato', sans-serif}.snackbar{position:fixed;right:2.4rem;bottom:0;left:2.4rem;z-index:200;display:flex;align-items:center;padding:1.4rem 2.4rem;margin:0 auto;margin-top:1.4rem;color:#fff;background-color:#212121;border-radius:.2rem;transition:all .35s cubic-bezier(0.4, 0, 0.2, 1) .35s}.snackbar.accept .button{color:#4caf50}.snackbar.warning .button{color:#eeff41}.snackbar.cancel .button{color:#f44336}.snackbar:not(.active){transform:translateY(100%)}.snackbar.active{transform:translateY(0%)}.label{flex-grow:1;font-size:1.4rem}.button{min-width:inherit;margin-top:-.7rem;margin-right:-1.2rem;margin-bottom:-.7rem;margin-left:4.8rem}



